import React from "react";
import { Link as ScrollLink } from "react-scroll";


function Pricebox({ pricebox: { name, price, icon, content, recommended } }) {
  return (
    <div
      className={
        recommended
          ? "price-item text-center recommended"
          : "price-item text-center"
      }
    >
      <i className={`price-icon ${icon}`}></i>
      <span className="plan">{name}</span>
      <h3 className="price">
        <em>€</em>
        {price}
        <span></span>
      </h3>
      <div className="price-content">
        <ul>
          {content.map((list, index) => (
            <li key={index}>{list}</li>
          ))}
        </ul>
      </div>
        <ScrollLink
          activeClass="active"
          to="section-contact"
          spy={true}
          smooth={true}
          offset={-50}
          duration={500} >
          <a className="btn btn-default">Contact me</a>
        </ScrollLink>
      
    </div>
  );
}

export default Pricebox;
